@import 'components/variables';

.section-vuelos {
  padding: 5% 0;
  .title-page {
    font-size: 3.5rem;
    font-weight: 900;
    color: $violet;
  }
  .accordion-button:not(.collapsed) {
    color: $violet;
    font-weight: bold;
    background-color: #f0d5f8;
  }
  .accordion-button {
    font-weight: bold;
    &:focus {
      border-color: #86b7fe;
      box-shadow: none
    }
  }
  .accordion-item:last-of-type {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .clean-filtro {
    padding: 2rem;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    border: 1px solid rgba(0,0,0,.125);
    border-top: 0;
    border-bottom-right-radius: .25rem;
    border-bottom-left-radius: .25rem;
    a {
      color: $text-gray;
    }
  }
  .search-more {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    a {
      color: $white;
      font-size: .8rem;
      transition: all .3s;
      &:hover {
        color: $yellow;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 30%;
      }
    }
  }
  .alarm {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    .content-alarm {
      width: 70%;
      p {
        font-size: .8rem;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 35%;
      }
    }
  }
  .nav-tabs .nav-link {
    color: $text-gray;
    font-weight: bold;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .nav-tabs .nav-link.active {
    color: $text-gray;
    font-weight: bold;
    background-color: #bfa1c7;
    border-color: #dee2e6 #dee2e6 #fff;
  }
  .nav-item {
    button {
      padding: .5rem 3rem;
      background-color: #e5d9e9;
    }
  }
  .nav-item:nth-child(2) {
    margin-left: 0;
    @media (min-width: 342px) {
      margin-left: .4rem;
    }
  }
  .first-column {
    display: none;
    @media (min-width: 768px) {
      display: block;
    }
  }
  .column-flight {
    border: 1px solid #b1b1b1;
    padding: 0;
    .content-text-column,
    .content-text-border {
      padding: .6rem 1.3rem;
      position: relative;
    }
    .content-text-gray {
      padding: .6rem 1.3rem;
      background-color: #c4c4c4;
      .img-copa {
        width: 5%;
        @media (min-width: 768px) {
          width: 15%;
        }
      }
    }
    .content-text-border {
      border-bottom: 1px solid #b1b1b1;
    }
    .movil-type {
      border: 1px solid #7f4391;
      padding: 0 10px;
      color: #7f4391;
      font-size: 11px;
      font-weight: 400;
      position: absolute;
      left: 20px;
      top: 0;
      display: block;
      transform: translateY(25%);
      @media (min-width: 425px) {
        transform: translateY(50%);
      }
      @media (min-width: 768px) {
        display: none;
      }
    }
  }
  .details-flight {
    border: 1px solid #b1b1b1;
    @media (min-width: 768px) {
      padding-right: 0;
    }
    .column-shaded-details-flight {
      background-color: #c4c4c4;
      display: flex;
      align-items: center;
      padding: .5rem 1rem;
      .fa-plane {
        color: $violet;
        font-size: 1.4rem;
        margin-right: .8rem;
      }
      .plane-left {
        transform: rotate(180deg);
      }
      .text-ida-vuelta {
        font-weight: bold;
      }
      .text-details-flight {
        font-size: .8rem;
      }
    }
    .column-details-flight {
      display: flex;
      align-items: center;
      padding: .5rem 1rem;
      .text-details-flight {
        font-size: .8rem;
      }
      .img-copa {
        width: 25%;
        margin-right: .5rem;
      }
      .text-hour-flight {
        font-weight: bold;
      }
      .arrow-flight {
        width: 100%;
      }
      .text-escala {
        font-size: .4rem;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%) translateY(-15%);
        @media (min-width: 455px) {
          font-size: .5rem;
        }
      }
      .time-flight {
        color: #b1b1b1;
        margin-left: 1rem;
        font-size: .8rem;
      }
      .more-info {
        font-size: .8rem;
        padding: .375rem .25rem;
        @media (min-width: 456px) {
          padding: .375rem .75rem;
        }
        a {
          font-size: .8rem;
          color: $text-gray;
        }
      }
    }
    .custom-flight {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1rem;
      border-top: 1px solid #b1b1b1;
      .btn-custom-flight {
        a {
          color: $text-gray;
        }
      }
    }
  }
  .col-right-package {
    border: 1px solid #b1b1b1;
    @media (min-width: 768px) {
      padding-left: 0;
    }
    .col-reservation {
      padding: 0 .5rem;
      .text-price {
        font-size: 1.2rem;
        margin-bottom: 0;
      }
      .price-big {
        color: $violet;
        font-size: 1.6rem;
        font-weight: 700;
        margin-bottom: 0;
      }
      .fa-shopping-bag {
        color: $yellow;
      }
      .fa-suitcase {
        color: $yellow;
        font-size: 1.3rem;
      }
      .fa-briefcase {
        color: #b1b1b1;
        font-size: 1.5rem;
      }
      .package-details {
        font-size: .8rem;
        margin-bottom: 0;
        color: #b1b1b1;
      }
    }
  }
}
#modalDetalleVuelo {
  z-index: 3050;
  .modal-content {
    .modal-header {
      background-color: $gray-light;
      .modal-title {
        font-weight: bold;
      }
    }
  }
}
#modalPaquetesVuelos {
  z-index: 3050;
  .modal-content {
    .modal-header {
      .modal-title {
        font-weight: bold;
        color: $violet;
        line-height: 0.7;
        strong {
          font-size: 1.2rem;
          color: $text-gray;
          font-weight: 600;
        }
      }
    }
    .modal-body {
      .package {
        border: 1px solid #b1b1b1;
        margin: .3rem;
        border-radius: 15px;
        .type-package {
          border-top-left-radius: 13px;
          border-top-right-radius: 13px;
          text-align: center;
          padding: .5rem 0;
          .title-type-package {
            font-weight: 700;
            font-size: 1.3rem;
            margin-bottom: 0;
          }
          .subtitle-type-package {
            margin-bottom: 0;
          }
        }
        .form-check-input {
          &:checked {
            background-color: $violet;
            border-color: $violet;
          }
          &:focus {
            border-color: $violet;
            box-shadow: none;
          }
        }
        .form-check-label {
          strong {
            font-size: 1.5rem;
          }
        }
        .details-package {
          padding: 1rem;
          background-color: $gray-light;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;
          .title {
            font-weight: 700;
            margin-bottom: .5rem;
          }
          ul {
            list-style: none;
            padding-left: 0;
            margin-bottom: 0;
            .fa-shopping-bag,
            .fa-suitcase,
            .fa-briefcase {
              color: green;
            }
            .fa-check,
            .fa-times {
              font-size: .7rem;
              color: green;
            }
          }
        }
      }
    }
  }
}